import $ from "jquery";
import {serialNumber, valorParametro} from "../def/function";

export default class Servidor {

   constructor(Pool){
      this.Pool = Pool;

      this.cnpj = null;
      this.serialNumber = null;
   }

   async api(nome, dados = {}, success = ()=>{}, error = ()=>{}){
      let url = "http://server.satflex.com.br/serv/terminal/" + nome + ".php";

      $.ajax({
         url: url,
         type: "POST",
         data: JSON.stringify(dados),
         success: (result) => {
            success(JSON.parse(result))
         },
         error: error
      });
   }

   async atualizarRequisicao(requisicao, success, fail){
      await this.carregarDados();

      let status = null;
      switch(requisicao.status){
         case "0":
				status = "1";
				break;
			case "1":
			case "2":
				status = "2";
				break;
         default:
            break;
      }

      this.api("atualizar_requisicao", {
         numeroserie: this.serialNumber,
         cnpj: this.cnpj,
         requisicao: {
				idrequisicao: requisicao.referencia,
				status: status,
				resposta: requisicao.resposta
         }
      }, (result) => {
         switch(result.status){
            case "0":
               success();
               break;
            case "2":
               fail(result.message);
               break;
            default:
               break;
         }
      }, () => {
         fail("Houve uma falha na conexo com o servidor.");
      });
	}

   async carregarDados(){
      if(!this.cnpj || !this.serialnumber){
         this.cnpj = await valorParametro(this.Pool, "EMITENTE", "CNPJ");
         this.serialNumber = await serialNumber();
      }
   }

   async verificarRequisicao(success, fail){
      await this.carregarDados();

      this.api("verificar_requisicao", {
         numeroserie: this.serialNumber,
         cnpj: this.cnpj
      }, (result) => {
         switch(result.status){
            case "0":
               success(result.requisicao);
               break;
            case "2":
               fail(result.message);
               break;
            default:
               break;
         }
      }, () => {
         fail("Houve uma falha na conexo com o servidor.");
      });
	}

	async verificarTerminal(){
      return await new Promise(async (resolve) => {
         await this.carregarDados();

         this.api("verificar_terminal", {
            numeroserie: this.serialNumber,
            cnpj: this.cnpj
         }, (result) => {
            switch(result.status){
               case "0":
                  resolve(result.terminal.status);
                  break;
               case "2":
                  resolve(false);
                  break;
               default:
                  break;
            }
         }, () => {
            resolve(false);
         });
      });
	}

}
