import React from "react";
import {Redirect} from "react-router";
import $ from "jquery";

import {Pool} from "../def/postgresql.js";

import Button from "../com/Button.js";
import Col from "../com/Col.js";
import Content from "../com/Content.js";
import FormGroup from "../com/FormGroup.js";
import Row from "../com/Row.js";
import Title from "../com/Title.js";

import Printer from "../com/Printer.js";

import {currentDate, decimaisQuantidade, defaultMessageBoxError, valorParametro} from "../def/function.js";

export default class RelOrcamento extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			dtinicial: null,
			dtfinal: null,
			redirect: null
		};

		this.acaoOrcamento = this.acaoOrcamento.bind(this);
		this.gerar = this.gerar.bind(this);
		this.imprimir = this.imprimir.bind(this);

		this.Pool = new Pool();
		this.Printer = new Printer(this.Pool);
	}

	acaoOrcamento(documento){
		let info = [
			"Nmero: " + String(documento.iddocumento).lpad(6, "0"),
			"Cliente: " + (documento.nomeorcamento ? documento.nomeorcamento : ""),
			"Quantidade: " + documento.totalquantidade.format(decimaisQuantidade(documento.totalquantidade), ",", "."),
			"Total: R$ " + documento.totaldocumento.format(2, ",", ".")
		].join("<br>");

		window.MessageBox.show({
			title: "O que voc deseja fazer com o oramento?",
			text: info,
			size: "lg",
			buttons: [
				{
					text: "Efetivar oramento",
					color: "green",
					icon: "thumbs-up",
					onClick: () => {
						window.MessageBox.hide();
						this.setState({
							redirect: "venda/" + documento.iddocumento
						});
					}
				},
				{
					text: "Excluir",
					color: "red",
					icon: "bin",
					className: "btn-sm",
					onClick: () => {
						window.MessageBox.show({
							title: "Excluso de oramento",
							text: "Tem certeza que deseja exclur o oramento?<br>Essa ao no poder ser desfeita.",
							buttons: [
								{
									text: "Sim",
									color: "green",
									icon: "thumbs-up",
									onClick: async () => {
										window.MessageBox.hide();
										window.Loading.show();
										try{
											await this.Pool.query("DELETE FROM documento WHERE iddocumento = $1", [documento.iddocumento]);
											await this.gerar();
										}catch(error){
											defaultMessageBoxError(error);
										}finally{
											window.Loading.hide();
										}
									}
								},
								{
									text: "No",
									color: "red",
									icon: "thumbs-down",
									onClick: () => {
										window.MessageBox.hide();
									}
								}
							]
						});
					}
				},
				{
					text: "Voltar",
					color: "blue",
					icon: "undo",
					onClick: function(){
						window.MessageBox.hide();
					}
				}
			]
		});
	}

	async gerar(){
		if(!$("#dtinicial").val()){
			window.MessageBox.show({
				text: "Por favor, informe a data inicial do fechamento antes de prosseguir."
			});
			return false;
		}
		if(!$("#dtfinal").val()){
			window.MessageBox.show({
				text: "Por favor, informe a data final do fechamento antes de prosseguir."
			});
			return false;
		}

		await this.prepararImpressao();

		this.setState({
			dtinicial: $("#dtinicial").val(),
			dtfinal: $("#dtfinal").val()
		});
	}

	async imprimir(){
		try{
			await this.Printer.alimentar(3);
			await this.Printer.guilhotina();
			await this.Printer.imprimir();
			window.FastMessage.show("Relatrio impresso com sucesso!");
		}catch(err){
			defaultMessageBoxError(err);
		}
	}

	async prepararImpressao(){
		await this.Printer.reiniciar();

		// Formata as datas
		let dtinicial = $("#dtinicial").val().split("-").reverse().join("/");
		let dtfinal = $("#dtfinal").val().split("-").reverse().join("/");

		// Cabecalho
		await this.Printer.texto(await valorParametro(this.Pool, "EMITENTE", "NOMEFANTASIA"), "center", true);
		await this.Printer.texto("ORAMENTOS: " + (dtinicial === dtfinal ? dtinicial : dtinicial + "  " + dtfinal), "center", true);
		await this.Printer.alimentar();
	}

	render(){
		if(this.state.redirect){
			return <Redirect to={this.state.redirect} />
		}

		let disabledImprimir = (!this.state.dtinicial || !this.state.dtfinal);

		return (
			<Content className="RelOrcamento">
				<Title>Relatrio de Oramento</Title>
				<Row>
					<Col size="6" offset="3">
						<Row>
							<FormGroup type="date" id="dtinicial" label="Data inicial" className="col-6" defaultValue={currentDate()} />
							<FormGroup type="date" id="dtfinal" label="Data final" className="col-6" defaultValue={currentDate()} />
						</Row>
						<Row>
							<Col size="6" className="pr-0">
								<Button text="Gerar" block={true} color="green" icon="file-play" onClick={this.gerar} />
							</Col>
							<Col size="6" className="pl-0">
								<Button text="Imprimir" block={true} color="blue" icon="printer" onClick={this.imprimir} disabled={disabledImprimir} />
							</Col>
						</Row>
					</Col>
				</Row>
				<RelOrcamentoRelatorio Pool={this.Pool} Printer={this.Printer} dtinicial={this.state.dtinicial} dtfinal={this.state.dtfinal} acaoOrcamento={this.acaoOrcamento} />
			</Content>
		)
	}
}

class RelOrcamentoRelatorio extends React.Component {

	constructor(props){
		super(props);

		this.state = {
			dados: []
		};
	}

	async carregarDados(props){
		props = (props === undefined ? this.props : props);

		// Monta a query
		let query = [
			"SELECT iddocumento, totalquantidade, totaldocumento, nomeorcamento,",
			"  dthrcriacao::DATE as dtcriacao, dthrcriacao::TIME as hrcriacao",
			"FROM documento",
			"WHERE operacao = $1",
			"  AND dthrcriacao::DATE BETWEEN $2 AND $3",
			"ORDER BY dthrcriacao"
		].join(" ");

		// Executa comando SQL
		let res = await props.Pool.query(query, ["OR", props.dtinicial, props.dtfinal]);

		// Determina o tamanho de cada coluna
		let colunas = this.props.Printer.colunas;
		let colIdDocumento = 7;
		let colTotalQuantidade = 6;
		let colTotalDocumento = 10;
		let colNomeOrcamento = colunas - colIdDocumento - colTotalQuantidade - colTotalDocumento;

		// Cria o cabecalho da grade na impressao
		let texto = "Nmero".substr(0, colIdDocumento).rpad(colIdDocumento, " ");
		texto += "Cliente".substr(0, colNomeOrcamento).cpad(colNomeOrcamento, " ");
		texto += "Qtde".substr(0, colTotalQuantidade).cpad(colTotalQuantidade, " ");
		texto += "Total".substr(0, colTotalDocumento).lpad(colTotalDocumento, " ");
		await this.props.Printer.texto(texto, "left", true);

		// Alimenta classe da impressora
		let t_totalquantidade = 0;
		let t_totaldocumento = 0;
		for(let i in res.rows){
			let row = res.rows[i];

			let iddocumento = String(row.iddocumento).lpad(6, "0");
			let nomeorcamento = (row.nomeorcamento === null ? "" : String(row.nomeorcamento));
			let totalquantidade = row.totalquantidade.format(decimaisQuantidade(row.totalquantidade), ",", ".");
			let totaldocumento = row.totaldocumento.format(2, ",", ".");

			texto = iddocumento.substr(0, colIdDocumento).rpad(colIdDocumento, " ");
			texto += nomeorcamento.substr(0, colNomeOrcamento).rpad(colNomeOrcamento, " ");
			texto += totalquantidade.substr(0, colTotalQuantidade).cpad(colTotalQuantidade, " ");
			texto += totaldocumento.substr(0, colTotalDocumento).lpad(colTotalDocumento, " ");
			await this.props.Printer.texto(texto);

			t_totalquantidade += parseFloat(row.totalquantidade);
			t_totaldocumento += parseFloat(row.totaldocumento);
		}

		// Calcula as casas decimais
		t_totalquantidade = t_totalquantidade.format(decimaisQuantidade(t_totalquantidade), ",", ".");
		t_totaldocumento = t_totaldocumento.format(2, ",", ".");

		// Totalizacao
		texto = "Total".substr(0, (colIdDocumento + colNomeOrcamento)).rpad((colIdDocumento + colNomeOrcamento), " ");
		texto += String(t_totalquantidade).substr(0, colTotalQuantidade).cpad(colTotalQuantidade, " ");
		texto += String(t_totaldocumento).substr(0, colTotalDocumento).lpad(colTotalDocumento, " ");
		await this.props.Printer.texto(texto, "left", true);
		await this.props.Printer.alimentar(2);

		// Atualiza o State com os dados
		this.setState({
			dados: res.rows
		});
	}

	componentWillUpdate(nextProps){
		if(nextProps !== this.props){
			this.carregarDados(nextProps);
		}
	}

	render(){
		if(!this.props.dtinicial || !this.props.dtfinal){
			return null;
		}

		let t_totalquantidade = 0;
		let t_totaldocumento = 0;

		return (
			<Row className="mt-5">
				<Col size="8" offset="2">
					<table className="table table-striped mb-5">
						<thead>
							<tr>
								<th className="text-center">Nmero</th>
								<th className="text-left">Cliente</th>
								<th className="text-center">Quantidade</th>
								<th className="text-center">Total</th>
							</tr>
						</thead>
						<tbody>
							{this.state.dados.map((e, i) => {
								t_totalquantidade += parseFloat(e.totalquantidade);
								t_totaldocumento += parseFloat(e.totaldocumento);

								return (
									<tr key={i} onClick={() => {this.props.acaoOrcamento(e)}}>
										<td className="text-center">{String(e.iddocumento).lpad(6, "0")}</td>
										<td className="text-left">{e.nomeorcamento}</td>
										<td className="text-center">{e.totalquantidade.format(decimaisQuantidade(e.totalquantidade), ",", ".")}</td>
										<td className="text-right">{"R$ " + e.totaldocumento.format(2, ",", ".")}</td>
									</tr>
								)
							})}
						</tbody>
						<tfoot>
							<tr>
								<th></th>
								<th></th>
								<th className="text-center">{t_totalquantidade.format(decimaisQuantidade(t_totalquantidade), ",", ".")}</th>
								<th className="text-right">{"R$ " + t_totaldocumento.format(2, ",", ".")}</th>
							</tr>
						</tfoot>
					</table>
				</Col>
			</Row>
		);
	}
}
