import Processo from "./Processo.js";
import Servidor from "./Servidor.js";

import { isDev, serialNumber, temporaryDirectory, valorParametro } from "../def/function.js";

const fs = window.require("fs");
const os = window.require("os");
const ftp = window.require("ftp");
const childProcess = window.require("child_process");

export default class ProcessoAutomatico {

	constructor(Pool){
		this.Pool = Pool;
		this.interval = null;
		this.running = false;

		window.ProcessoAutomatico = this;

		this.executandoBackup = false;
	}

	async iniciar(){
		this.interval = setInterval(async () => { this.executar() }, 1000 * 60); // Cria o intervalo para executar a cada 1 minuto
		setTimeout(async () => { this.executar() }, 1000 * 10); // Executa uma primeira vez 10s depois de iniciar
	}

	pausar(){
		clearInterval(this.interval);
	}

	async executar(){
		if(this.running){
			return;
		}
		try {
			this.running = true;
			await this.executarRefreshView();
			await this.executarVerificarDtVerificacao();
			await this.executarVerificarTerminal();
			if(!isDev()){
				await this.executarBackup();
			}
		}catch(err){
			throw err;
		}finally{
			this.running = false;
		}
	}

	async executarBackup(){
		// Verifica se ja nao tem backup em execucao
		if(this.executandoBackup){
			return;
		}

		// Carrega e verifica o processo
		let processo = new Processo(this.Pool, "BACKUP");
		if(!await processo.verificarIntervalo()){
			return;
		}

		// Carrega os modulos
		if(this.fs === undefined) this.fs = fs;
		if(this.os === undefined) this.os = os;
		if(this.ftp === undefined) this.ftp = ftp;
		if(this.childProcess === undefined) this.childProcess = childProcess;

		// Se macos, nao efetua backup
		if(this.os.platform() === "darwin"){
			return;
		}

		// Captura os dados de conexao as FTP
		let dadosFTP = null;
		try{
			dadosFTP = {
				host: await valorParametro(this.Pool, "BACKUP", "HOST"),
				port: await valorParametro(this.Pool, "BACKUP", "PORTA"),
				user: await valorParametro(this.Pool, "BACKUP", "USUARIO"),
				password: await valorParametro(this.Pool, "BACKUP", "SENHA")
			};
		}catch(err){
			console.log("Erro ao efetuar backup: no foi possvel carregar os parmetros do FTP.");
			return false;
		}

		// Define que esta executando backup
		this.executandoBackup = true;

		// Define a localizacao do pg_dump
		let pgDump = null;
		switch(this.os.platform()){
			case "win32":
				pgDump = "C:/Program Files/PostgreSQL/14/bin/pg_dump.exe";
				if(!this.fs.existsSync(pgDump)) pgDump = "C:/Program Files/PostgreSQL/13/bin/pg_dump.exe";
				if(!this.fs.existsSync(pgDump)) pgDump = "C:/Program Files/PostgreSQL/12/bin/pg_dump.exe";
				if(!this.fs.existsSync(pgDump)) pgDump = "C:/Program Files/PostgreSQL/11/bin/pg_dump.exe";
				if(!this.fs.existsSync(pgDump)) pgDump = "C:/Program Files/PostgreSQL/10/bin/pg_dump.exe";
				if(!this.fs.existsSync(pgDump)) pgDump = "C:/Program Files/PostgreSQL/9.6/bin/pg_dump.exe";
				if(!this.fs.existsSync(pgDump)) pgDump = "C:/Program Files/PostgreSQL/9.3/bin/pg_dump.exe";
				break;
			default:
				break;
		}

		// Define o diretorio de trabalho
		let tempDir = temporaryDirectory();

		// Verifica se o diretorio existe
		if(!this.fs.existsSync(tempDir)){
			this.fs.mkdirSync(tempDir);
		}

		// Limpa os backups do diretorio temporario
		let files = this.fs.readdirSync(tempDir);
		for(let file of files){
			if(file.substr(-7) === ".backup"){
				this.fs.unlinkSync(tempDir + "/" + file);
			}
		}

		// Executa o backup via pg_dump
		let date = new Date();
		let currTimeStr = date.getFullYear() + "-" + String(date.getMonth() + 1).lpad(2, "0") + "-" + String(date.getDate()).lpad(2, "0") + "-" + String(date.getHours()).lpad(2, "0") + "-" + String(date.getMinutes()).lpad(2, "0") + "-" + String(date.getSeconds()).lpad(2, "0");
		let fileName = tempDir + "/" + currTimeStr + ".backup";
		let command = "\"" + pgDump + "\" -F c -b -w -U postgres -f \"" + fileName + "\" satflex";
		this.childProcess.execSync(command);

		// Caputura o numero de serie do terminal
		if(this.serialNumber === undefined){
			this.serialNumber = await serialNumber();
		}

		// Define o arquivo local e remoto
		let localFile = fileName;
		let remoteFile = "/" + this.serialNumber + "/" + currTimeStr + ".backup";

		// Copia o arquivo para o servidor de backup
		let client = new this.ftp();
		client.on("ready", () => {
			client.cwd("/", () => {
				client.mkdir(this.serialNumber, true, (err) => {
					if(err){
						this.executandoBackup = false;
						throw err;
					}
					client.put(localFile, remoteFile, false, (err) => {
						if(err) throw err;
						client.end();
						processo.atualizarProcesso();
						this.executandoBackup = false;
					});
				});
			});
		}).connect(dadosFTP);
	}

	async executarRefreshView(){
		// Carrega e verifica o processo
		let processo = new Processo(this.Pool, "REFRESH_VIEW");
		if(!await processo.verificarIntervalo()){
			return;
		}

		// Atualiza as views materializadas
		let views = ["v_vendaprodutodiario", "v_vendapagamentodiario"];
		for(let view of views){
			await this.Pool.query("REFRESH MATERIALIZED VIEW " + view);
		}

		// Atualiza o processo no banco de dados
		await processo.atualizarProcesso();
	}

	async executarVerificarDtVerificacao(){
		// Carrega e verifica o processo
		let processo = new Processo(this.Pool, "VERIFICAR_DTVERIFICACAO");
		if(!await processo.verificarIntervalo()){
			return;
		}

		// Captura as datas para comparacao
		let dtverificacao = await valorParametro(this.Pool, "SISTEMA", "DTVERIFICACAO");
		let dtatual = new Date();

		// Verifica se ja passou mais de 10 dias sem comunicar com o servidor
		if(dtverificacao !== null && dtverificacao.length > 0){
			dtverificacao = new Date(dtverificacao);
		}else{
			dtverificacao = null;
		}
		if(dtverificacao === null || ((dtatual.getTime() - dtverificacao.getTime()) / (1000 * 3600 * 24)) > 10){
			// Atualiza status do sistema para: Bloqueado por falta de comunicacao
			let sql = "UPDATE parametro SET valor = $3 WHERE grupo = $1 AND nome = $2";
			this.Pool.query(sql, ["SISTEMA", "STATUS", "0002"]);
		}

		// Atualiza o processo no banco de dados
		await processo.atualizarProcesso();
	}

	async executarVerificarTerminal(){
		// Carrega e verifica o processo
		let processo = new Processo(this.Pool, "VERIFICAR_TERMINAL");
		if(!await processo.verificarIntervalo()){
			return;
		}

		// Verifica o terminal no servidor
		if(!this.servidor) this.servidor = new Servidor(this.Pool);
		let status = await this.servidor.verificarTerminal();
		if(status === false){
			console.log("Houve uma falha ao verificar o terminal no servidor.");
		}else{
			// Atualiza o status do terminal
			this.Pool.query("UPDATE parametro SET valor = $3 WHERE grupo = $1 AND nome = $2", ["SISTEMA", "STATUS", status]);

			// Atualiza a data de verificacao
			let dtatual = new Date().toISOString().substr(0, 10);
			this.Pool.query("UPDATE parametro SET valor = $3 WHERE grupo = $1 AND nome = $2", ["SISTEMA", "DTVERIFICACAO", dtatual]);
		}

		// Atualiza o processo no banco de dados
		await processo.atualizarProcesso();
	}

}
