import { valorParametro } from "../def/function.js";

const fs = window.require("fs");

export default class Email {

	constructor(Pool){
		this.Pool = Pool;

		this._anexos = [];
		this._destinatario = null;
		this._titulo = null;
		this._remetente = null;
	}

	anexo(filename){
		this._anexos.push({
			nome: filename.split("/").reverse()[0],
			conteudo: Buffer.from(fs.readFileSync(filename)).toString('base64')
		});
	}

	corpo(corpo){
		this._corpo = corpo;
	}

	destinatario(destinatario){
		if(Array.isArray(destinatario)){
			destinatario = destinatario.join("; ");
		}
		this._destinatario = destinatario;
	}

	async enviar(success, fail){
		let cnpj = await valorParametro(this.Pool, "EMITENTE", "CNPJ");

		const data = {
			remetente_email: 'satflex@controlware.com.br',
			remtente_nome: this._remetente,
			destinatario: this._destinatario,
			assunto: this._titulo,
			corpo: this._corpo,
			anexos: this._anexos
		};
		console.log({ data });

		$.ajax({
			url: 'https://andromeda.controlware.com.br/api/email/enviar',
			type: 'POST',
			beforeSend: request => {
				request.setRequestHeader("idchave", "7ba93327-465f-41ee-975a-f208d4995bf1");
				request.setRequestHeader("cnpj", cnpj);
			},
			data,
			success: () => success(),
			error: result => {
				const response = JSON.parse(result.responseText);
				if(response){
					if(!response.message && response.erro){
						response.message = response.erro;
					}
					fail(response);
				}else{
					fail({ message: result.statusText });
				}
			}
		});
	}

	remetente(remetente){
		this._remetente = remetente;
	}

	titulo(titulo){
		this._titulo = titulo;
	}

}
