import ftp from "ftp";
import { applicationDirectory, defaultMessageBoxError, valorParametro } from "../def/function.js";

const fs = window.require("fs");
const electron = window.require("electron");

export default class Atualizacao {

	constructor(Pool){
		this.Pool = Pool;

		this.atualizarAplicativoParcial = this.atualizarAplicativoParcial.bind(this);
		this.atualizarBancoDeDados = this.atualizarBancoDeDados.bind(this);
		this.baixarAplicativoParcial = this.baixarAplicativoParcial.bind(this);
		this.verificarAtualizacao = this.verificarAtualizacao.bind(this);

		this.fs = fs;
	}

	async atualizarAplicativoParcial(callBack){
		this.baixarAplicativoParcial(async (err) => {
			if(err) return callBack(err);
			await this.atualizarBancoDeDados((err) => {
				if(err) return callBack(err);

				// Sucesso
				callBack();
			});
		});
	}

	async atualizarBancoDeDados(callBack){
		if(this.ftp === undefined){
			this.ftp = ftp;
		}

		// Funcao usada para ler um arquivo no FTP sem necessitar de callback
		let ftpReadSync = async (client, filename) => {
			return new Promise((resolve) => {
				client.get(filename, false, (err, stream) => {
					if(err) throw new Error(err);

					let chunks = [];
					stream.on("data", (chunk) => {
						chunks.push(chunk.toString());
					}).on("end", () => {
						resolve(chunks.join(""));
					});
				});
			});
		};

		// Verifica ultima instrucao executada
		let paramAtualizacaoInstrucaoSQL = parseInt(await valorParametro(this.Pool, "ATUALIZACAO", "INSTRUCAOSQL"), 10);

		try{
			// Conecta no servidor FTP
			let client = new this.ftp();
			client.on("ready", () => {
				client.list("/sql", false, async (err, list) => {
					if(err) return callBack(err);

					// Verifica quais instrucoes devem ser executadas
					let arrArquivoSQL = [];
					for(let file of list){
						let numeroInstrucaoSQL = parseInt(file.name.replace(".sql", ""), 10);
						if(numeroInstrucaoSQL > paramAtualizacaoInstrucaoSQL){
							arrArquivoSQL.push(file.name);
						}
					}
					arrArquivoSQL.sort();

					// Abre os arquivos e atualiza o banco de dados
					try{
						for(let i in arrArquivoSQL){
							let arquivoSQL = arrArquivoSQL[i];
							let instrucaoSQL = await ftpReadSync(client, "/sql/" + arquivoSQL);
							console.log("Executar SQL: \n"+instrucaoSQL);
							await this.Pool.query(instrucaoSQL);
							console.log("SQL Finalizada");
						}
						callBack();
					}catch(err){
						console.log("Erro ao atualizar BD: " + err.message);
						callBack(err);
					}
				});
			}).connect({
				host: "websac.net",
				port: "21",
				user: "satflex-update",
				password: "automacao"
			});
		}catch(err){
			callBack(err);
		}
	}

	baixarAplicativoParcial(callBack){
		if(window.__ignoreAppUpdate === true){
			return callBack();
		}

		if(this.childProcess === undefined){
			this.childProcess = window.require('childProcess');
		}

		this.childProcess.exec("git --version", [], (err) => {
			if(err) return callBack(new Error("git-not-found"));

			let gitDir = applicationDirectory() + "/.git";
			console.log("Diretrio .git: " + gitDir);

			try{
				this.childProcess.execSync("git config --global git.user \"satflex@controlware.com.br\"");
			}catch(err){
				return callBack(err);
			}

			let spawn = this.childProcess.spawn("cd \"" + applicationDirectory() + "\" && git pull", {shell: true});
			spawn.stdout.on("data", (data) => {
				console.log("stdout: " + data.toString());
			});
			spawn.stderr.on("data", (data) => {
				console.log("stderr: " + data.toString());
			});
			spawn.on("close", (data) => {
				console.log("exit: " + data.toString());
				callBack();
			});
			/*
			this.childProcess.exec(commands.join(" && "), [], (err) => {
				if(err){
					callBack(err);
				}else{
					callBack();
				}
			});
			*/
		});
	}

	baixarGit(){
		window.MessageBox.show({
			title: "Git no est instalado",
			text: "Para atualizar o SAT-Flex,  necessrio que o Git esteja instalado.",
			buttons: [
				{
					text: "Baixar o Git",
					color: "green",
					icon: "github",
					onClick: () => {
						window.MessageBox.hide();
						let shell = electron.shell;
						shell.openExternal("ftp://instalacao:123@websac.net/SATFlex/Git-2.18.0-32-bit.exe");
					}
				},
				{
					text: "Mais tarde...",
					color: "blue",
					icon: "undo",
					onClick: () => {
						window.MessageBox.hide();
					}
				}
			]
		});
	}

	versaoAtual(){
		let fileName = applicationDirectory() + "/package.json";
		return window.require(fileName).version;
	}

	verificarAtualizacao(){
		window.MessageBox.show({
			title: "Atualizao do SAT-Flex",
			text: "Deseja iniciar a atualizao do SAT-Flex agora?<br>Isso pode levar alguns minutos.",
			buttons: [
				{
					text: "Sim",
					icon: "thumbs-up",
					color: "green",
					onClick: () => {
						window.MessageBox.hide();
						window.Loading.show();
						console.log("Inicio da atualizao: " + new Date().toLocaleString());
						this.atualizarAplicativoParcial((err) => {
							console.log("Fim da atualizao: " + new Date().toLocaleString());
							window.Loading.hide();

							if(err){
								if(err.message === "git-not-found"){
									this.baixarGit();
								}else{
									defaultMessageBoxError(err);
								}
								return false;
							}

							window.MessageBox.show({
								title: "Atualizado com sucesso!",
								text: "O seu SAT-Flex foi atualizado com sucesso!<br>A aplicao ser reinciada.",
								buttons: [{
									text: "Ok",
									icon: "thumbs-up",
									onClick: () => {
										window.require('electron').ipcRenderer.sendSync("restart");
									}
								}]
							});
						});
					}
				},
				{
					text: "No",
					icon: "thumbs-down",
					color: "red",
					onClick: () => {
						window.MessageBox.hide();
					}
				}
			]
		});
	}

}
