import { default as xlsx } from "node-xlsx";
import {defaultMessageBoxError} from "../def/function.js";

const electron = window.require("electron");
const remote = window.require("@electron/remote");

export default class Importacao {

	constructor(Pool){
		this.Pool = Pool;
	}

	corAleatoria(){
		let cores = [
			"#b71c1c", "#d32f2f", "#f44336", "#e57373", "#ffcdd2",
			"#880e4f", "#c2185b", "#e91e63", "#f06292", "#f8bbd0",
			"#4a148c", "#7b1fa2", "#9c27b0", "#ba68c8", "#e1bee7",
			"#311b92", "#512da8", "#673ab7", "#9575cd", "#d1c4e9",
			"#1a237e", "#303f9f", "#3f51b5", "#7986cb", "#c5cae9",
			"#0d47a1", "#1976d2", "#2196f3", "#64b5f6", "#bbdefb",
			"#01579b", "#0288d1", "#03a9f4", "#4fc3f7", "#b3e5fc",
			"#006064", "#0097a7", "#00bcd4", "#4dd0e1", "#b2ebf2",
			"#004d40", "#00796b", "#009688", "#4db6ac", "#b2dfdb",
			"#194D33", "#388e3c", "#4caf50", "#81c784", "#c8e6c9",
			"#33691e", "#689f38", "#8bc34a", "#aed581", "#dcedc8",
			"#827717", "#afb42b", "#cddc39", "#dce775", "#f0f4c3",
			"#f57f17", "#fbc02d", "#ffeb3b", "#fff176", "#fff9c4",
			"#ff6f00", "#ffa000", "#ffc107", "#ffd54f", "#ffecb3",
			"#e65100", "#f57c00", "#ff9800", "#ffb74d", "#ffe0b2",
			"#bf360c", "#e64a19", "#ff5722", "#ff8a65", "#ffccbc",
			"#3e2723", "#5d4037", "#795548", "#a1887f", "#d7ccc8",
			"#263238", "#455a64", "#607d8b", "#90a4ae", "#cfd8dc",
			"#000000", "#525252", "#969696", "#D9D9D9", "#FFFFFF"
		];
		return cores[Math.floor(Math.random() * cores.length)];
	}

	async incluirCategoria(client, categoria){
		let cor = this.corAleatoria();

		let sql = "INSERT INTO categoria (descricao, idncm, sinctributacao, cor) VALUES ($1, $2, $3, $4) RETURNING idcategoria";
		let res = await client.query(sql, [categoria.descricao, categoria.idncm, 'S', cor]);

		return res.rows[0].idcategoria;
	}

	async incluirProduto(client, produto){
		console.log(produto);

		// Trata alguns valores recebidos
		if(produto.balanca)	produto.balanca = produto.balanca.substr(0, 1).toUpperCase();
		if(produto.precovariavel) produto.precovariavel = produto.precovariavel.substr(0, 1).toUpperCase();
		if(produto.preco) produto.preco = produto.preco.toFloat();
		if(produto.aliqicms) produto.aliqicms = produto.aliqicms.toFloat();
		if(produto.aliqpis) produto.aliqpis = produto.aliqpis.toFloat();
		if(produto.aliqcofins) produto.aliqcofins = produto.aliqcofins.toFloat();

		// Balanca para unidade de medida
		produto.idunidade = (produto.balanca === "S" ? 2 : 1);
		delete produto.balanca;

		// Preenche o preco com zero caso venha vazio
		if(!produto.preco){
			produto.preco = 0;
		}

		// Reformata o NCM
		if(produto.codigoncm){
			produto.codigoncm = String(produto.codigoncm).removeFormat();
			produto.codigoncm = produto.codigoncm.substr(0, 4) + "." + produto.codigoncm.substr(4, 2) + "." + produto.codigoncm.substr(6, 2);
		}

		// Reformata o CEST
		if(produto.cest){
			produto.cest = produto.cest.removeFormat();
			produto.cest = produto.cest.substr(0, 2) + "." + produto.cest.substr(2, 4) + "." + produto.cest.substr(6, 2);
		}

		// Reformata o CFOP
		if(produto.cfop){
			produto.cfop = produto.cfop.removeFormat();
			produto.cfop = produto.cfop.substr(0, 1) + "." + produto.cfop.substr(1, 3);
		}

		// Verifica se deve sincronizar a tributacao
		console.log([produto.csosn, produto.cstpis, produto.cstcofins, (produto.csosn || produto.cstpis || produto.cstcofins)]);
		if(produto.csosn || produto.cstpis || produto.cstcofins){
			produto.sinctributacao = "N";
		}else{
			produto.sinctributacao = "S";
		}

		// Carrega o ID do NCM
		if(produto.codigoncm && !produto.idncm){
			let res = await client.query("SELECT idncm FROM ncm WHERE codigoncm = $1", [produto.codigoncm]);
			if(res.rows.length === 0){
				throw new Error("O NCM " + produto.codigoncm + " no foi encontrado na base de NCMs do SAT-Flex.");
			}
			produto.idncm = res.rows[0].idncm;
		}

		// Verifica se deve consultar a categoria
		if(produto.categoria && !produto.idcateogria){
			let res = await client.query("SELECT idcategoria FROM categoria WHERE descricao = $1", [produto.categoria]);
			if(res.rows.length === 0){
				// Cria a categoria e retorna o ID
				produto.idcategoria = await this.incluirCategoria(client, {
					descricao: produto.categoria,
					idncm: produto.idncm
				});
			}else{
				produto.idcategoria = res.rows[0].idcategoria;
			}
		}

		// Remove as colunas que nao vao entrar no SQL
		delete produto.codigoncm;
		delete produto.categoria;

		// Caputura as colunas e os valores
		let columns = Object.keys(produto);
		let values = Object.values(produto);
		let references = [];
		for(let i = 1; i <= values.length; i++){
			references.push("$" + String(i));
		}

		// Cria SQL de inclusao do produto e executa
		let sql = "INSERT INTO produto (" + columns.join(", ") + ") VALUES (" + references.join(", ") + ")";
		await client.query(sql, values);

		// Incrementa o contador de produto importados
		this.importados++;
	}

	async processarPlanilha(fileName, callBack){
		// Abre um client com o banco de dados
		const client = await this.Pool.connect();

		try{
			// Inicia a transacao
			client.query("BEGIN");

			// Carrega a planilha
			let planilha = xlsx.parse(fileName);

			// Captura os dados a serem lidos
			let dados = planilha[0].data;

			// Remove a primeira linha (cabecalho)
			dados.shift();

			// Verifica se existe ao menos um produto
			if(typeof dados !== "object" || dados.length === 0 || dados[0].length === 0){
				throw new Error("Nenhum produto foi encontrado na planilha.");
			}

			// Percorre as linhas da planilha
			for(let linha of dados){
				if(!linha[0]){
					continue;
				}

				let produto = {
					descricao: linha[0],
					categoria: linha[1],
					codigoean: linha[2],
					balanca: linha[3],
					precovariavel: linha[4],
					preco: linha[5],
					codigoncm: linha[6],
					origem: linha[7],
					csosn: linha[8],
					aliqicms: linha[9],
					cstpis: linha[10],
					aliqpis: linha[11],
					cstcofins: linha[12],
					aliqcofins: linha[13],
					codcontribsocial: linha[14],
					cest: linha[15],
					cfop: linha[16]
				};

				// Tenta gravar o produto
				await this.incluirProduto(client, produto);
			}

			// Confirma a transacao do banco de dados
			await client.query("COMMIT");

			// Sucesso
			callBack();
		}catch(err){
			// Cancela a transacao no banco
			await client.query("ROLLBACK");
			callBack(err);
		}finally{
			// Libera o client com o banco
			client.release();
		}
	}

	selecionarPlanilha(){
		let { dialog } = remote;
		dialog.showOpenDialog({
			title: "Selecone a planilha de produtos",
			filters: [
				{name: "MS-Excel", extensions: ["xls", "xlsx"]}
			]
		}, (filePaths) => {
			if(filePaths && filePaths.length > 0){
				this.importados = 0;
				window.Loading.show();
				this.processarPlanilha(filePaths[0], (err) => {
					window.Loading.hide();
					if(err) return defaultMessageBoxError(err);
					window.MessageBox.show({
						title: "Planilha importada com sucesso!",
						text: "A planilha de produtos foi importada com sucesso.<br>Total de produtos: " + String(this.importados)
					});
				});
			}
		});
	}

}
