import React from "react";

import Col from "./Col.js";
import Row from "./Row.js";

import {defaultMessageBoxError} from "../def/function.js";

export default class PainelRapidoPesquisa extends React.Component {

	constructor(props){
		super(props);

		this.state = {
			pesquisa: props.pesquisa, // Atual pesquisa
			produtos: [], // Lista de produtos visiveis
			totalproduto: null // Total do produto caso seja um produto de balanca
		};

		this.carregarProdutos = this.carregarProdutos.bind(this);
	}

	carregarProdutos(pesquisa){
		pesquisa = (pesquisa ? pesquisa : this.props.pesquisa);
		if(typeof pesquisa !== "string" || pesquisa.length === 0){
			this.setState({
				produtos: []
			});
			return;
		}

		let codigoean = null;
		let totalproduto = null;

		let where = [];
		if(pesquisa.length === 13 && pesquisa.substr(0, 1) === "2"){
			codigoean = parseInt(pesquisa.substr(1, 6), 10);
			totalproduto = parseInt(pesquisa.substr(7, 5), 10) / 100;
			pesquisa = codigoean;
			where.push("produto.codigoean = $1");
		}else{
			where.push("produto.descricao ILIKE '%'||$1||'%'");
			where.push("produto.codigoean = $1");
		}

		this.totalproduto = totalproduto;

		let query = [
			"SELECT produto.idproduto, produto.descricao, produto.precovariavel,",
			"  produto.preco, categoria.cor, unidade.precisao, unidade.descricao AS unidade",
			"FROM produto",
			"INNER JOIN categoria USING (idcategoria)",
			"INNER JOIN unidade USING (idunidade)",
			"WHERE " + where.join(" OR "),
			"ORDER BY descricao"
		].join(" ");

		this.props.Pool.query(query, [pesquisa], (err, res) => {
			if(err){
				defaultMessageBoxError(err);
				return false;
			}
			this.setState({
				produtos: res.rows
			});
		});
	}

	componentDidMount(){
		this.carregarProdutos();
	}

	componentWillReceiveProps(nextProps){
		this.carregarProdutos(nextProps.pesquisa);
	}

	render(){
		let className = ["painelrapido", "painelrapido-pesquisa"];

		if(this.props.layoutVendas === "0"){
			className.push("painelrapido-poucosprodutos");
		}

		// Define se deve selecionar o produto ao desenhar
		// Criado para situacao em que o usuario leu um codigo de barras e ja deve carregar o produto
		let selecionarProduto = (!isNaN(this.props.pesquisa) && this.state.produtos.length === 1);

		return (
			<div className={className.join(" ")}>
				<Row className="painelrapido-produto">
					{this.state.produtos.map((produto, i) => {
						return <Produto key={i} {...produto} onSelectProduto={this.props.onSelectProduto} layoutVendas={this.props.layoutVendas} selecionarProduto={selecionarProduto} totalproduto={this.totalproduto} />
					})}
				</Row>
			</div>
		);
	}

}

class Produto extends React.Component {

	constructor(props){
		super(props);

		this.onClick = this.onClick.bind(this);
	}

	componentDidMount(){
		//console.log(this.props.selecionarProduto);
		if(this.props.selecionarProduto){
			this.props.onSelectProduto(this.props, null, this.props.totalproduto);
		}
	}

	onClick(event){
		if(this.props.onSelectProduto){
			this.props.onSelectProduto(this.props, event, this.props.totalproduto);
		}
	}

	render(){
		let size = (this.props.layoutVendas === "0" ? 3 : 2);

		return (
			<Col size={size}>
				<div className="painelrapido-produto-item" style={{borderColor: this.props.cor}} onClick={this.onClick}>
					<span>{this.props.descricao}</span>
				</div>
			</Col>
		);
	}
}
