import React from "react";

import {Pool} from "../def/postgresql.js";

import Button from "../com/Button.js";
import Col from "../com/Col.js";
import Content from "../com/Content.js";
import Row from "../com/Row.js";
import Title from "../com/Title.js";

import Importacao from "../com/Importacao.js";
import Printer from "../com/Printer.js";

const electron = window.require("electron");

export default class Ferramentas extends React.Component {

	constructor(props){
		super(props);

		this.state = {
			redirect: null
		};

		this.descobrirPortaImpressora = this.descobrirPortaImpressora.bind(this);
		this.importarPlanilha = this.importarPlanilha.bind(this);

		this.Pool = new Pool();
	}

	consoleDesenvolvedor(){
		electron.ipcRenderer.sendSync("open-dev-tools");
	}

	descobrirPortaImpressora(){
		window.Loading.show();
		new Promise(async (resolve) => {
			let printer = new Printer(this.Pool);
			for(let i = 1; i <= 20; i++){
				let nome = "COM" + String(i);
				let texto = "Porta configurada para impressora: " + nome + " ".repeat(90);
				try{
					await printer.imprimir(nome, texto, "escpos");
				}catch(err){
					continue;
				}
			}
			resolve();
		}).then(() => {
			window.Loading.hide();
			window.FastMessage.show("Texto enviado para as portas COM");
		});
	}

	importarPlanilha(){
		let importacao = new Importacao(this.Pool);
		importacao.selecionarPlanilha();
	}

	render(){
		return (
			<Content>
				<Title>Ferramentas</Title>
				<Row>
					<Col size="8" offset="2">
						<Row>
							<Col size="6" className="mb-4">
								<Button text="Descobrir porta<br>COM da impressora" icon="printer" size="xxl" block={true} onClick={this.descobrirPortaImpressora} />
							</Col>
							<Col size="6" className="mb-4">
								<Button text="Importar planilha<br>de produtos" icon="download" size="xxl" block={true} onClick={this.importarPlanilha} />
							</Col>
							<Col size="6" className="mb-4">
								<Button text="Abrir console de<br>desenvolvedor" icon="embed2" size="xxl" block={true} onClick={this.consoleDesenvolvedor} />
							</Col>
						</Row>
					</Col>
				</Row>
			</Content>
		)
	}
}
