import {applicationDirectory} from  "../def/function.js";

const fs = window.require("fs");
const childProcess = window.require("child_process");

export default class ComDLL {

	constructor(){
		this.appPath = applicationDirectory();
		this.childProcess = childProcess;
		this.fs = fs;

		this.srcPath = this.appPath + "/src";
		this.comDLLPath = this.srcPath + "/lib/comdll-1.0.0";
	}

	execute(dllPath, method, args, xml){
		try{
			let commandExec = this.comDLLPath + "/ComDLL.exe";
			let commandArgs = [
				this.srcPath + "/" + dllPath,
				method
			].concat(args);

			let xmlFile = this.comDLLPath + "/entrada.xml";
			let returnFile = this.comDLLPath + "/retorno.txt";

			if(this.fs.existsSync(xmlFile)){
				this.fs.unlinkSync(xmlFile);
			}
			if(this.fs.existsSync(returnFile)){
				this.fs.unlinkSync(returnFile);
			}

			if(xml !== undefined){
				this.fs.writeFileSync(xmlFile, xml, "utf8");
			}
			this.childProcess.execFileSync(commandExec, commandArgs, {
				timeout: 60000
			});
			let result = this.fs.readFileSync(returnFile);

			return result.toString().trim();
		}catch(err){
			switch(err.code){
				case "ETIMEDOUT":
					throw new Error("O tempo mximo de comunicao com o equipamento fiscal SAT foi excedido.<br>Desconecte todos os cabos conectados ao SAT, espere 5 segundos, conecte-os de volta e tente efetuar a operao novamente.<br><br>Caso o problema persista, entre em contato com o suporte.");
				default:
					throw err;
			}
		}
	}

}
